import Cookie from "js-cookie";
import { Base64 } from "js-base64";

import { ValidationObserver, ValidationProvider } from "vee-validate";
export default {
	middleware: ["session-control", "admin"],
	layout: "admin",
	components: {
		ValidationObserver,
		ValidationProvider,
	},
	computed: {
		img_url() {
			return process.env.apiPublicUrl;
		},
	},
	data() {
		return {
			data: [],
			searchTitle: null,
			headers: [
				{ text: "#", align: "center", value: "rank" },
				{ text: "Adı", align: "center", value: "name" },
				{ text: "Durum", align: "center", value: "isActive" },
				{
					text: "İşlemler",
					align: "center",
					value: "actions",
					sortable: false,
				},
			],
			page: 1,
			totalPages: 0,
			pageSize: 25,
			pageSizes: [25, 50, 100, 200, 500, 1000],
			loading: false,
			userData: !this.isEmpty(Cookie.get("userData"))
				? JSON.parse(Base64.decode(Cookie.get("userData")))
				: null,
		};
	},
	methods: {
		isEmpty(obj) {
			if (typeof obj == "number") return false;
			else if (typeof obj == "string") return obj.length == 0;
			else if (Array.isArray(obj)) return obj.length == 0;
			else if (typeof obj == "object")
				return obj == null || Object.keys(obj).length == 0;
			else if (typeof obj == "boolean") return false;
			else return !obj;
		},
		getRequestParams(searchTitle, page, pageSize) {
			let params = {};
			params["title"] = searchTitle;
			params["page"] = page;
			params["size"] = pageSize;
			return params;
		},
		retrieveData(url) {
			let urlParam = "get-all";
			if (!this.isEmpty(url)) {
				urlParam = url;
			}
			const params = this.getRequestParams(
				this.searchTitle,
				this.page,
				this.pageSize
			);
			this.$axios
				.get(
					`${process.env.apiBaseUrl}panel/datatables/${urlParam}?table=criteria&page=${params.page}&per_page=${params.size}&search=${params.title}&search_columns=name`,
					{
						json: true,
						withCredentials: false,
						mode: "no-cors",
						headers: {
							"Access-Control-Allow-Origin": "*",
							"Access-Control-Allow-Headers":
								"Origin, Content-Type, X-Auth-Token, Authorization",
							"Access-Control-Allow-Methods":
								"GET, POST, PATCH, PUT, DELETE, OPTIONS",
							"Access-Control-Allow-Credentials": true,
							"Content-type": "application/json",
							Authorization: "Bearer " + this.userData.api_token,
						},
						credentials: "same-origin",
					}
				)
				.then((response) => {
					this.data = response.data.data.data.map(this.getDisplayData);

					this.totalPages = response.data.data.last_page;
				})
				.catch((err) => console.log(err))
				.finally(() => (this.loading = false));
		},
		handlePageChange(value) {
			this.page = value;
			this.retrieveData();
		},
		handlePageSizeChange(size) {
			this.pageSize = size;
			this.page = 1;
			this.retrieveData();
		},
		refreshList() {
			this.retrieveData();
		},
		editData(id) {
			this.$router.push("/panel/criterias/update/" + id);
		},
		deleteData(id) {
			this.$axios
				.delete(
					process.env.apiBaseUrl +
						"panel/datatables/delete?table=criteria&id=" +
						id,
					{
						json: true,
						withCredentials: false,
						mode: "no-cors",
						headers: {
							"Access-Control-Allow-Origin": "*",
							"Access-Control-Allow-Headers":
								"Origin, Content-Type, X-Auth-Token, Authorization",
							"Access-Control-Allow-Methods":
								"GET, POST, PATCH, PUT, DELETE, OPTIONS",
							"Access-Control-Allow-Credentials": true,
							"Content-type": "application/json",
							Authorization: "Bearer " + this.userData.api_token,
						},
						credentials: "same-origin",
					}
				)
				.then((response) => {
					if (response.data.success) {
						this.$izitoast.success({
							title: response.data.title,
							message: response.data.msg,
							position: "topCenter",
						});
						this.refreshList();
					} else {
						this.$izitoast.error({
							title: response.data.title,
							message: response.data.msg,
							position: "topCenter",
						});
					}
				});
		},
		isActiveSetter(id) {
			this.$axios
				.get(
					process.env.apiBaseUrl +
						"panel/datatables/is-active-setter?table=criteria&id=" +
						id,
					{
						json: true,
						withCredentials: false,
						mode: "no-cors",
						headers: {
							"Access-Control-Allow-Origin": "*",
							"Access-Control-Allow-Headers":
								"Origin, Content-Type, X-Auth-Token, Authorization",
							"Access-Control-Allow-Methods":
								"GET, POST, PATCH, PUT, DELETE, OPTIONS",
							"Access-Control-Allow-Credentials": true,
							"Content-type": "application/json",
							Authorization: "Bearer " + this.userData.api_token,
						},
						credentials: "same-origin",
					}
				)
				.then((response) => {
					if (response.data.success) {
						this.$izitoast.success({
							title: response.data.title,
							message: response.data.msg,
							position: "topCenter",
						});
						this.refreshList();
					} else {
						this.$izitoast.error({
							title: response.data.title,
							message: response.data.msg,
							position: "topCenter",
						});
					}
				});
		},
		getDisplayData(data) {
			return {
				rank: data.rank,
				id: data._id,
				name: data.name,
				isActive: data.isActive,
			};
		},
	},
	mounted() {
		this.retrieveData();
	},
};
